@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote::before,blockquote::after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}a,del,ins{text-decoration:none}a{color:inherit}label,button{cursor:pointer}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}input,button{outline:0}

body{background-color:#212121;color:#fff;font-size:16px;font-family:'Roboto', 'Courier New', Courier, monospace}

.app{width:90%;margin:0 auto;transition:1s}@media screen and (min-width: 768px){.app{width:70%}}@media screen and (min-width: 999px){.app{width:60%}}

.header{background-color:#262626;margin-top:1rem;display:flex;justify-content:space-between;padding:1rem;position:relative}.header-title{font-size:24px;text-transform:uppercase;font-weight:bold;font-style:italic;background:linear-gradient(to right, #EE811E, #DC2060);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent;width:40%}@media screen and (min-width: 768px){.header-title{width:25%}.header-list{display:flex;list-style:none;justify-content:space-around;align-items:center;font-size:14px;width:65%}.header-list-item a{text-decoration:none;margin:0 1px}.header-list-item a:hover{color:#DC2060}}@media screen and (min-width: 1200px){.header-title{width:25%}}

.nav{transition:.2s;position:absolute;right:20px;top:15px;font-size:25px;color:#EE811E;cursor:pointer}.nav-burger{z-index:300;position:absolute;right:0}.nav-links{position:fixed;background:#131418;height:100vh;width:100%;flex-direction:column;clip-path:circle(50px at 90% -20%);-webkit-clip-path:circle(50px at 90% -10%);transition:all 1s ease-out;pointer-events:none;display:flex;list-style:none;padding:0 0.7vw;align-items:center;text-transform:uppercase;left:0;top:0;z-index:200}.nav-links.open{clip-path:circle(1000px at 90% -10%);-webkit-clip-path:circle(1000px at 90% -10%);pointer-events:all}.nav-links li{opacity:0}.nav-links li:nth-child(1){transition:all 0.5s ease 0.2s}.nav-links li:nth-child(2){transition:all 0.5s ease 0.4s}.nav-links li:nth-child(3){transition:all 0.5s ease 0.6s}.nav-links li:nth-child(4){transition:all 0.5s ease 0.7s}li.fade{opacity:1;margin-top:120px}.nav-links li a::before{content:"";display:block;height:3px;width:0%;background-color:#DC2060;position:absolute;transition:all ease-in-out 250ms;margin:0 0 0 10%}.nav-links li a{text-decoration:none;margin:0 0.7vw}.nav-links li a:hover{color:#DC2060}


.entete{background-color:#262626;margin-top:1rem;padding:1rem;display:flex;flex-direction:column;align-items:center}.entete-container--infos{align-items:center;display:flex;flex-direction:column}.entete-title{text-transform:uppercase;font-weight:200;letter-spacing:.05rem;margin:1rem 0;font-size:18px}.entete-losange{-webkit-clip-path:polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);clip-path:polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);width:250px;margin:1rem 0}.entete-job{background:linear-gradient(to right, #EE811E, #DC2060);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent;font-size:18px}.entete-button{background:linear-gradient(to right, #EE811E, #DC2060);border:none;padding:.5rem;color:#fff;border-radius:5px;margin:2rem 0 1rem}@media screen and (min-width: 768px){.entete{flex-direction:row-reverse}.entete-losange{width:300px}.entete-title{font-size:20px}.entete-job{font-size:20px}.entete-container--img{display:flex;justify-content:center;width:50%}.entete-container--infos{justify-content:space-evenly;width:50%;height:400px}.entete-button{font-size:14px;letter-spacing:.1rem}}

.fiche{background-color:#262626;margin:1rem 0}.fiche-container-infos{padding:1rem}.fiche-title{color:#D78833;font-weight:600;letter-spacing:.05rem}.fiche-subtitle{margin:3rem 0 1rem;font-size:18px;text-transform:uppercase;letter-spacing:.1rem;font-weight:600}.fiche-desc{margin:2rem 0 1rem;text-align:justify;line-height:1.3rem;letter-spacing:.02rem;word-break:break-word}.fiche-img{display:none}@media screen and (min-width: 768px){.fiche{display:flex}.fiche-subtitle{font-size:22px}.fiche-container-infos{display:flex;flex-direction:column}.fiche-img{padding:0;margin:0;width:100%;display:block}}

